@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;500;600;700&display=swap');

body {
  font-family: 'Open Sans', Arial, Helvetica, sans-serif;
}

/* Fonts */
.font-general-regular {
  font-family: 'Open Sans', Arial, Helvetica, sans-serif;
  font-weight: 400;
}

.font-general-medium {
  font-family: 'Open Sans', Arial, Helvetica, sans-serif;
  font-weight: 500;
}

.font-general-semibold {
  font-family: 'Open Sans', Arial, Helvetica, sans-serif;
  font-weight: 600;
}

.font-general-bold {
  font-family: 'Open Sans', Arial, Helvetica, sans-serif;
  font-weight: 700;
}

/* Scroll to top style */
.scrollToTop {
  @apply bg-indigo-600;
  @apply text-white;
  position: fixed;
  width: 100%;
  align-items: center;
  height: 20px;
  justify-content: center;
  z-index: 999;
  cursor: pointer;
  animation: fadeIn 0.3s;
  transition: opacity 0.4s;
  opacity: 0.9;
}

.scrollToTop:hover {
  opacity: 1;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.5;
  }
}
